@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
:root {
    --montserrat-font: "Montserrat", sans-serif;

    --yoda-gradient: linear-gradient(-45deg, #ff0099, #493240);
    --pigypink-gradient: linear-gradient(45deg, #ee9ca7, #f0abb4fb, #ffdde1);
    --forest-gradient: linear-gradient(to right, #000428, #004e92);
    --dull-gradient: linear-gradient(to right, #c9d6ff, #e2e2e2);
    --mystic-gradient: linear-gradient(65deg, #757f9a, #d7dde8);
    --khaki-gradient: linear-gradient(
        to right bottom,
        #e0a015,
        #f0b000,
        #ebc748,
        #fadf87,
        #fcda7dda
    );

    --midnightCity-gradient: linear-gradient(to bottom, #232526, #414345);
    --deepSpace-gradient: linear-gradient(to bottom, #000000, #434343);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: #f8f4ee;
}

a {
    text-decoration: none;
    color: inherit;
}


.footer-container{display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%;height:50vh;position:relative;background:var(--midnightCity-gradient)}.footer-container .footer-social-wrapper{display:flex;justify-content:center;align-items:center;flex-direction:row;width:100%;padding:20px}.footer-container .footer-social-wrapper .footer-social-icon{transition:1s;margin:3%}.footer-container .footer-social-wrapper .footer-social-icon:hover{transform:translateY(-8px);transition:1s}.footer-container .footer-email-subscribe{display:flex;justify-content:center;align-items:center;flex-direction:row;width:100%;height:10vh}.footer-container .footer-email-subscribe #footer-form{display:flex;justify-content:center;align-items:center;flex-direction:row;width:80%;margin:auto;margin:10px 0}.footer-container .footer-email-subscribe #footer-form #footer-email-label{font-size:2em;color:#f8f4ee;margin-right:10px;padding:5px}.footer-container .footer-email-subscribe #footer-form #footer-email-input{min-width:100px;width:30%;border-radius:0;outline:none;border:none;padding:5px;height:3vh;text-transform:capitalize;margin:5px}.footer-container .footer-email-subscribe #footer-form .submit-footer-form-btn{margin:5px}.footer-container .footer-contact-info{display:flex;justify-content:center;align-items:center;flex-direction:column;color:#f8f4ee;border:2px groove #c5c5c5;padding:10px 15px;margin:15px 0}.footer-container .footer-contact-info span{display:flex;margin:5px 0}.footer-container .footer-contact-info span h5{margin-right:5px}.footer-container .footer-contact-info span p{letter-spacing:0.1em}.footer-container .footer-outro{display:flex;justify-content:center;align-items:center;flex-direction:row;width:100%;color:#f8f4ee;margin:auto;font-size:0.7em;height:5vh;position:absolute;bottom:0}.footer-container .footer-outro a{text-decoration:none;color:#f8f4ee;transition:0.5s;font-size:1.1em}.footer-container .footer-outro a:hover{color:#f07;transition:0.5s}.footer-container .footer-outro p{margin:0 5px;font-size:1.1em;font-weight:200;letter-spacing:0.2em}.footer-container .saysomthing{background:var(--pigypink-gradient);-webkit-background-clip:text;-webkit-text-fill-color:transparent;font-size:1em;text-transform:capitalize}@media (max-width: 400px){.footer-contact-info{font-size:0.8em}#footer-form{width:90%}#footer-form *{font-size:1em}#foote-email-label{font-size:1em}#footer-email-input{font-size:0.5em;width:90%}.submit-footer-form-btn *{font-size:0.9em !important}.saysomthing{font-size:1em}}


.error-container{position:relative;width:100%;height:100vh;display:flex;justify-content:center;align-items:center;background:black;overflow:hidden}@media (max-width: 500px){.error-img{width:100%}}

.db-link{position:relative;display:inline-block;min-width:160px;height:50px;box-shadow:0 5px 25px rgba(0,0,0,0.25);font-weight:bold;font-family:"Montserrat",sans-serif}.db-link button{display:flex;justify-content:center;align-items:center;flex-direction:row;position:absolute;top:0;left:0;width:100%;height:100%;color:#f8f4ee;background:#1f1f1f;text-transform:uppercase;letter-spacing:2px;font-size:18px;cursor:pointer;border:none;outline:none}.db-link button:nth-child(2){color:#000000;background:#f8f4ee;overflow:hidden;z-index:2;transition:0.5s;-webkit-clip-path:polygon(0% 0%, 40% 0, 55% 50%, 40% 100%, 0% 100%);clip-path:polygon(0% 0%, 40% 0, 55% 50%, 40% 100%, 0% 100%)}.db-link button:nth-child(2):hover{-webkit-clip-path:polygon(0% 0%, 100% 0, 100% 49%, 100% 100%, 0% 100%);clip-path:polygon(0% 0%, 100% 0, 100% 49%, 100% 100%, 0% 100%)}.db-link button:nth-child(1):hover ~ button:nth-child(2){-webkit-clip-path:polygon(0% 0%, 0 0, 0 50%, 0 100%, 0% 100%);clip-path:polygon(0% 0%, 0 0, 0 50%, 0 100%, 0% 100%)}

.nav-links {
    display: flex;
    list-style: none;
    flex: 1 1 auto;
}

.nav-links li {
    height: 100%;
    padding: 0 20px;
}

.nav-links li a {
    text-decoration: none;
    text-transform: uppercase;
    font-size: 1.3em;
    color: white;
}

.logo{
    width: 55px;
    padding: 5px 0;
}
.cart-container{display:flex;justify-content:center;align-items:center;flex-direction:column;width:100%;margin-top:100px}

.WMHero-Container {
    overflow-x: hidden;
}

.hero-img-wrapper {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
}

.home-header-pic{
    width: 100%;
}
.home-header-pic img{
    width: 100%;
}

.WHHero-label-man {
    position: absolute;
    top: 50%;
    color: white;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
    transition: 0.8s;
    cursor: pointer;
    font-size: 7em;
    text-decoration: line-through;
}
.WHHero-label-woman {
    position: absolute;
    top: 50%;
    color: white;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
    transition: 0.5s;
    cursor: pointer;
    font-size: 7em;
    text-decoration: line-through;
}

.WHHero-label-man:hover {
    color: black;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
    transition: 0.5s;
    text-decoration: none;
}

.WHHero-label-woman:hover {
    color: black;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
    transition: 0.5s;
    text-decoration: none;
}


@media(max-width:510px){
    .WHHero-label-man {
   
    font-size: 4em;
}
.WHHero-label-woman {

    font-size: 4em;
}
}
.home-header-container {
    margin-top: 70px;
    position: relative;
    overflow: hidden;
}

.header-img-wrapper {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-header-img {
    transition: 0.5s;
    width: 100%;
}

.home-header-img:hover {
    opacity: 0.9;
    transition: 0.5s;
}

.home-header-img img {
    width: 100%;
}
.home-header-content-wrapper {
    position: absolute;
    color: white;
    text-transform: uppercase;
    top: 50%;
    font-size: 1.8em;
    font-weight: bolder;
    text-align: center;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.home-header-button-wrapper {
    margin-top: 15px;
}

@media (max-width: 1200px) {
    .home-header-img {
        height: unset;
    }

    .home-header-content-wrapper {
        font-size: 1.3em;
    }
}
@media (max-width: 1000px) {
    .home-header-img img {
        width: unset;
    }
    .home-header-img {
        width: unset;
    }
}
@media (max-width: 440px) {
    .home-header-content-wrapper {
        font-size: 1em;
    }
}

.accessories-container {
    width: 100%;
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.accessories-container img {
    width: 100%;
    height: 100vh;
    opacity: 0.7;
}

.accessories-btn {
    position: absolute;
}

@media (max-width: 1200px) {
    .accessories-container img {
        width: 100%;
        height: unset;
    }

}

.scroll-arrow {
    font-size: 2em !important;
    cursor: pointer;
    margin: 0 10px;
}

.scroling-picture {
    padding: 10px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1s;
}

.scroling-picture img {
    opacity: 0.9;
}

.scroling-picture:hover {
    opacity: 0.89;
    transition: 1s;
}

.scrolling-img-btn {
    position: absolute;
}
.scrolling-img-btn > * {
    font-weight: bolder !important;
    text-transform: capitalize;
}

