}


.products-container{margin:auto;display:flex;justify-content:center;align-items:center;flex-direction:row;overflow:hidden;width:100%}.products-container .product-card{display:flex;justify-content:center;align-items:center;flex-direction:column}.products-container .product-card .product-img{width:250px;height:350px;transition:1s ease}.products-container .product-card:hover .product-img{width:270px;transform:translateY(-10px)}.products-container .product-card:hover .product-title{font-size:1.05em;transition:0.5s}.products-container .product-card .product-title{width:250px;text-align:center;padding:15px 15px;border-radius:5px;background:#f7f7f7;margin-bottom:15px;font-size:1em;transition:0.5s}.products-container .product-card .card-footer .product-category{text-transform:capitalize;color:#013464;padding:10px 0}.products-container .product-card .card-footer .product-category span{color:#646464}.products-container .product-card .card-footer .product-price{padding-bottom:10px;color:#120488}.products-container .product-card .card-footer .product-price span{color:#646464}.products-container .product-card .card-footer .mrl{margin:0 10px !important}.pp-container{display:flex;justify-content:space-between;align-items:center;margin-top:15vh;overflow:hidden}.pp-container .pp-item{display:flex !important;justify-content:center !important;align-items:center !important}

.shop-container{margin-top:70px;padding:100px 0;background:white}

